import { redirect } from "next/navigation";
import { createSupabaseServerClient } from "../../../lib/supabase/serverClient";
import { signOut } from "./actions";
import WorkspaceShell from "./workspace-shell";

export const dynamic = "force-dynamic";

export default async function WorkspacePage() {
  const supabase = await createSupabaseServerClient();
  const { data, error } = await supabase.auth.getSession();

  if (error) {
    throw error;
  }

  const session = data.session;
  const email = session?.user?.email;

  if (!session) {
    redirect("/signin");
  }

  return (
    <main className="mx-auto flex min-h-screen max-w-6xl flex-col gap-8 px-6 py-12">
      <header className="flex flex-wrap items-center justify-between gap-4">
        <div>
          <p className="text-sm font-semibold uppercase tracking-wide text-blue-600">Workspace</p>
          <h1 className="text-3xl font-bold leading-tight">Welcome back{email ? `, ${email}` : ""}</h1>
          <p className="text-slate-700">
            Signed in via Supabase. Hook the actions below to your existing backend and storage.
          </p>
        </div>
        <form action={signOut}>
          <button
            type="submit"
            className="rounded-lg border border-slate-200 px-4 py-2 text-sm font-semibold text-slate-700 shadow-sm hover:border-slate-300 hover:bg-slate-50"
          >
            Sign out
          </button>
        </form>
      </header>

      <WorkspaceShell />
    </main>
  );
}

